<template>
  <div>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>门店管理</span>
        </div>
      </el-card>
      <el-row>
        <el-form ref="form" :model="query" label-width="80px">
          <el-col :span="24">
              <el-form-item label="门店">
                <el-input v-model="query.key" @change="inquire" placeholder="门店名"></el-input>
              </el-form-item>
                <el-form-item label="服务范围">
                  <el-select v-model="query.services" @change="inquire" placeholder="请选择">
                    <el-option v-for="item in storeserviceslist" :key="item.id" :label="item.name" :value="item.id"></el-option>
                  </el-select>
                </el-form-item>
                  <el-form-item>
                    <el-button type="primary" @click="inquire">查询</el-button>
                  </el-form-item>
          </el-col>
        </el-form>
      </el-row>
      <el-row>
        <el-button type="primary" @click="add">添加</el-button>
        <el-button type="danger">批量删除</el-button>
      </el-row>

       <!-- 表格 -->
          <Table
            :loading="loading"
            :tableData="tableData"
            :tableCouml="tableCouml"
            @idshow="idshow">
            <!-- 图片 -->
            <template #logo="data">
              <el-image
              v-if="data.data.logo"
            style="width: 40px; height: 40px"
            :src="url+data.data.logo"
            fit="fill"></el-image>

            <el-image
            v-else
            style="width: 40px; height: 40px"
            src="https://img1.baidu.com/it/u=413643897,2296924942&fm=253&fmt=auto&app=138&f=JPEG?w=800&h=500"
            fit="fill"></el-image>
            </template>
            <!-- 服务范围 -->
            <template #services="data">
              <p v-if="data.data.services === '3'">服装鞋帽</p>
              <p v-if="data.data.services === '2'">蔬菜水果</p>
              <p v-if="data.data.services === '1'">早餐早点</p>
              <p v-if="data.data.services === ''">{{data.data.services}}</p>
              <!--  -->
            </template>
            <!-- 操作 -->
            <template #option="data">
                <el-button size="mini" type="success" @click="mod(data.data)">修改</el-button>
                <el-button size="mini" type="danger" @click="handleDelete(data.data.id)">删除</el-button>
            </template>
            </Table>

        <!-- 分页 -->
            <Paging :query="query" :total="total" @paging="paging"></Paging>
        <!-- 添加 -->
            <StoreAdd :dialogVisible="dialogVisible" @change="change" :tite="tite" :form="form"></StoreAdd>

  </div>
</template>

<script>
import Table from "@/components/commts/Table.vue";
import Paging from "@/components/commts/Paging.vue";
import StoreAdd from '@/components/stores/StoresAdd.vue'
  export default {
    data(){
      return{
        query:{
                page:1,
                psize:5
        },
        dialogVisible: false,
        storeserviceslist:[],
        loading: true,
        tableData: [],
        tableCouml: [
                { prop: "id", label: "ID",},
                { prop: "name", label: "门店",},
                { prop: "address", label: "地址",},
                { prop: "logo", label: "门店logo",isshow: true, showname: "logo"},
                { prop: "services", label: "服务范围",isshow:true,showname:"services"},
                { prop: "username", label: "店主",},
                { prop: "mobile", label: "联系电话",},
                { prop: "startTime", label: "营业时间",},
                { label: "操作", isshow: true, showname: "option" },
            ],
      total: 0,
      url:'http://estate.eshareedu.cn/estate/upload/',
      tite:'添加门店',
      form:{}

      }
    },
    components:{
        Table,
        Paging,
        StoreAdd
    },
    created(){
      this.getstoreservices()
      this.getstorelist()
    },
    methods:{
      // 删除
      async handleDelete(ids){
        // console.log(id)
      let data = {
        id:ids
      }
      let res =await this.http.get('/stores/delete',data)
      console.log(res);
      if(res.errCode === 10000){
        this.$message({
          message: '删除成功',
          type: 'success'
        });
        this.getstorelist()
      }
      },
      // 添加
      add(){
        this.dialogVisible = true
        this.form = {id:0}
        this.tite = '添加门店'
      },
      change(val){
        // console.log(val)
        this.dialogVisible = val
        this.getstoreservices()
      },
      // 查询
      inquire(){
        this.getstorelist()
      },
      paging(){
        this.getstorelist()
      },
       idshow(val){
            console.log(val)
        },
      // 修改
      mod(data){
        this.dialogVisible = true
        this.form = data
        this.tite = '修改门店'
      },
      // 经营范围
      async getstoreservices(){
        let res =await this.http.get('/storeservices/list')
        // console.log(res);
        if(res.errCode === 10000){
          this.storeserviceslist = res.data.list
        }
      },
      // 列表
      async getstorelist(){
        let res =await this.http.get('/stores/list',this.query)
        console.log(res);
        if(res.errCode === 10000){
          this.tableData = res.data.list
          this.total = res.data.counts
          this.loading = false
        }
      }
    }
  }
  
</script>

<style lang="less" scoped>
@import '@/assets/css/admin.css';
/deep/.el-card__header{
  border-bottom: none !important;
}
.el-col{
  display: flex;
}
img{
  width: 50px;
  height: 50px;
}
</style>