<template>
  <div>
      <el-dialog
        :title="tite"
        center
        :visible.sync="dialogVisible"
        width="60%"
        :before-close="handleClose">
        <div class="box">
          <el-form ref="form" :model="form" label-width="100px">
              <div class="box-left">
                <el-form-item label="店名：">
                  <el-input v-model="form.name" style="width:80%;"></el-input>
                </el-form-item>
                <el-form-item label="手机号：">
                  <el-input v-model="form.mobile" maxlength="11" style="width:80%;"></el-input>
                </el-form-item>
                <el-form-item label="营业时间：">
                   <el-time-select
                      style="width:30%;"
                      placeholder="起始"
                      v-model="form.startTime"
                      :picker-options="{
                        start: '08:30',
                        step: '00:15',
                        end: '18:30'
                      }">
                    </el-time-select>
                    <el-time-select
                    style="width:30%;"
                      placeholder="结束"
                      v-model="form.endTime"
                      :picker-options="{
                        start: '08:30',
                        step: '00:15',
                        end: '18:30',
                        minTime: startTime
                      }">
                    </el-time-select>
                </el-form-item>
                  <el-form-item label="服务范围">
                    <el-select v-model="form.services" placeholder="请选择活动区域">
                      <el-option v-for="item in storeserviceslist" :key="item.id" :label="item.name" :value="item.id"></el-option>
                    </el-select>
                  </el-form-item>
                   <el-form-item label="店主：">
                    <el-input v-model="form.username" style="width:80%;"></el-input>
                  </el-form-item>
                   <el-form-item label="门店介绍：">
                      <el-input type="textarea" rows="5" v-model="form.intro" style="width:80%;"></el-input>
                </el-form-item>
              </div>
              <div class="box-right">
                 <el-form-item label="门店地址：">
                    <el-input v-model="form.address" style="width:80%;"></el-input>
                  </el-form-item>
                  <el-form-item label="门店logo：">
                    <el-upload
                        class="avatar-uploader"
                        action="http://estate.eshareedu.cn/estate/api/upload/add"
                        :show-file-list="false"
                        :headers="token"
                        :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload">
                        <img v-if="imageUrl" :src="imageUrl" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                      </el-upload>
                  </el-form-item>
                  <el-form-item label="门店图片：">
                    <el-upload
                      class="upload-demo"
                      action="http://www.eshareedu.cn/estate/api/upload/editeradd"
                      :on-success="hangesuccess"
                      :on-preview="handlePreview"
                      :on-remove="handleRemove"
                      :file-list="fileList"
                      :headers="token"
                      multiple
                      list-type="picture">
                      <el-button size="small" type="primary">点击上传</el-button>
                      <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                    </el-upload>
                  </el-form-item>
              </div>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="funselect">取 消</el-button>
          <el-button type="primary" @click="submit">确 定</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
  export default {
    props:{
      dialogVisible:{
        typeof:Boolean,
        Reflect:()=>falses
      },
      tite:{
        typeof:Number,
        Reflect:'添加门店'
      },
      form:{
        typeof:Object,
        Reflect:()=>{}
      }
    },
    data(){
      return{
        url:'http://estate.eshareedu.cn/estate/upload/',
        startTime: '',
        endTime: '',
        storeserviceslist:'',
        fileList:[],
        token:{
          Authorization:""
        },
        imageUrl:'',
        // images:[]
      }
    },
    created(){
      this.getstoreservices()
      this.token.Authorization = sessionStorage.getItem('token')
    },
    methods:{
      // 点击添加
      async submit(){
        // console.log(123);
        // let data = {
        //   id: 0,
        //   name: this.form.name,
        //   mobile: this.form.mobile,
        //   pass: null,
        //   tel: this.form.mobile,
        //   logo: this.form.logo,
        //   address: this.form.address,
        //   startTime: this.form.startTime,
        //   endTime: this.form.endTime,
        //   services: this.form.services,
        //   username: this.form.username,
        //   intro: this.form.intro,
        //   images: null
        // }
        // console.log(data);

        let res =await this.http.post('/stores/add',this.form)
        // console.log(res)
        if(res.errCode === 10000){
            this.$message({
              message: '添加成功',
              type: 'success'
            });
          this.$emit('change', false);
        }else{
           this.$message({
              message: '添加失败',
              type: 'error'
            });
        }
      }, 
       // 经营范围
      async getstoreservices(){
        let res =await this.http.get('/storeservices/list')
        // console.log(res);
        if(res.errCode === 10000){
          this.storeserviceslist = res.data.list
        }
      },
      // 上传单张
        handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
        this.form.logo = res.data
        // console.log(this.form.logo);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      },
      //上传多张
       handleRemove(file, fileList) {
        // console.log(file, fileList);
      },
      handlePreview(file) {
        // console.log(file);
      },
      hangesuccess(file,fileList){
        
        this.images.push({url:fileList.name})
        // console.log(this.images);
      },
      // 取消
      funselect(){ 
        this.$emit('change', false);
      },
      // 关闭添加
        handleClose(done) {
        this.$confirm('确认关闭？')
          .then(()=> {
            this.$emit('change', false);
          })
          .catch(() => {});
      },
      // async getdetail(id){
      //       let res = await this.http.get('/stores/get',{id:id})
      //       this.$refs.editor.html = res.data.content
      //   }
    },
    watch:{
        form:{
          handler(newval){
            console.log(newval);
            this.$nextTick(()=>{
              if(newval.id){
                this.imageUrl = this.url + newval.logo
                // this.getdetail(this.images)
                console.log(this.imageUrl);
              }
            })
          },
          deep:true
        }
      }
  }
</script>

<style lang="less" scoped>
.box{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .el-form{
    width: 100%;
    display: flex;
  }
  .box-left{
    width: 50%;
  }
  .box-right{
    width: 50%;
  }
}
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 90px;
    height: 90px;
    line-height: 90px;
    text-align: center;
  }
  .avatar {
    width: 90px;
    height: 90px;
    display: block;
  }
  /deep/.el-upload{
    border: 1px dashed #ccc !important;
  }
</style>